<template>
  <b-modal id="ticketCreate" ref="ticketCreate" class="ticketCreate" title=" "
           body-class="pt-0"
           hide-footer @shown.once="show" no-close-on-esc>

    <div slot="modal-header-close">
      <img class="icon" src="/icons/close.svg" alt="Close">
    </div>

    <b-form @submit.prevent="createTicket">
      <b-row class="mt-3 mb-5">

        <b-col cols="8">
          <label for="inputTitle">:</label>
          <b-form-input id="inputTitle"
                        v-model.trim="ticket.title"
                        type="text"
                        placeholder=" "
                        required></b-form-input>
        </b-col>
        <b-col cols="4">
          <label for="inputApplicant"> :</label>
          <input-search id="inputApplicant"
                        placeholder=" "
                        v-model.trim="ticket.applicant_name"
                        @selectItem="ticket.applicant_id=$event"
                        searchTable="employees"
                        :additionalSearchConditionals="[{field:'type_id',value:2}]"
                        searchField="name">
          </input-search>
        </b-col>

        <b-col cols="4" class="mt-3">
          <label for="selectType"> :</label>
          <form-select id="selectType" v-model="ticket.type_id" :options="ticketTypes" required></form-select>
        </b-col>
        <b-col cols="4" class="mt-3">
          <label for="selectPriority"> :</label>
          <form-select id="selectPriority" v-model="ticket.priority_id" :options="ticketPriorities"></form-select>
        </b-col>
        <b-col cols="4" class="mt-3">
          <label for="inputContractor">:</label>
          <input-search id="inputContractor"
                        placeholder=" "
                        v-model.trim="ticket.employee_name"
                        @selectItem="ticket.employee_id=$event"
                        searchTable="employees"
                        :additionalSearchConditionals="[{field:'type_id',value:1}]"
                        searchField="name">
          </input-search>
        </b-col>

        <b-col cols="12" class="mt-3">
          <label for="inputDescription"> :</label>
          <b-form-textarea id="inputDescription"
                           v-model="ticket.description"
                           placeholder=" "
                           :rows="3"
                           :max-rows="6"
                           no-resize
                           required>
          </b-form-textarea>
        </b-col>

        <b-col cols="12" class="mt-3">
          <label for="inputComment">:</label>
          <b-form-textarea id="inputComment"
                           v-model="ticket.comment"
                           placeholder=" ,     :  , , "
                           :rows="2"
                           :max-rows="4"
                           no-resize>
          </b-form-textarea>
        </b-col>

      </b-row>

      <b-btn type="submit" variant="primary" size="lg"></b-btn>

    </b-form>

  </b-modal>
</template>

<script>
  export default {
    name: "TicketCreate",
    data() {
      return {
        ticket: {},
        ticketTypes: [],
        ticketPriorities: [],
        ticketStatuses: [],
      }
    },
    methods: {
      show() {
        this.loadDictionaries();
      },
      loadDictionaries() {
        axios.get('ticketTypes')
          .then(response => this.ticketTypes = response.data);
        axios.get('ticketPriorities')
          .then(response => this.ticketPriorities = response.data);
        axios.get('ticketStatuses')
          .then(response => this.ticketStatuses = response.data);
      },
      createTicket() {
        axios.post('tickets', this.postParams())
          .then(response => {
            this.alertSuccess();
            this.ticket = {};
            this.$emit('ticketCreated', response.data);
            this.$refs.ticketCreate.hide();
          })
      },
      postParams() {
        let ticket = this.ticket;
        delete ticket.employee_name;
        return ticket;
      },
    },
  }
</script>

<style lang="scss">
  .ticketCreate {
    .modal-dialog {
      max-width: 1050px;
    }
    .input-group {
      width: 350px;
    }
    label {
      font-weight: bold;
    }
  }
</style>



