<template>
  <div>

    <b-button variant="success" v-b-modal.ticketCreate> </b-button>
    <ticket-create @ticketCreated="ticketCreated"></ticket-create>

    <b-table class="mt-4"
             hover :items="tickets.data"
             :fields="fields"
             tbody-tr-class="pointer"
             @row-clicked="editTicket">

      <template slot="customer" slot-scope="data">
        {{ _.get(data.item, 'applicant.name', data.item.applicant_name) }}
      </template>
      <template slot="contractor" slot-scope="data">
        {{ _.get(data.item, 'employee.name', ' ') }}
      </template>
      <template slot="status" slot-scope="data">
        <div class="badge badge-primary status-badge"
             :style="{ 'background-color': _.get(data.item, 'status.rgb') }">
          {{ _.get(data.item, 'status.name', ' ') }}
        </div>
      </template>

    </b-table>

    <b-pagination size="md"
                  :total-rows="tickets.total"
                  v-model="tickets.current_page"
                  :per-page="tickets.per_page"
                  @input="changePage">
    </b-pagination>

  </div>
</template>

<script>
  import TicketCreate from './TicketCreate';

  export default {
    components: {
      TicketCreate,
    },
    data() {
      return {
        tickets: [],
        fields: [
          {
            key: 'id',
            label: '',
            sortable: false,
          },
          {
            key: 'title',
            label: '',
            sortable: false,
          },
          {
            key: 'customer',
            label: '',
            sortable: false,
          },
          {
            key: 'contractor',
            label: '',
            sortable: false,
          },
          {
            key: 'status',
            label: '',
            sortable: false,
          },
        ],
      }
    },
    mounted() {
      this.fetchData();
    },
    methods: {
      fetchData(page = 1) {
        axios.get('tickets/?page=' + page)
          .then(response => this.tickets = response.data)
      },
      ticketCreated(ticket) {
        this.tickets.data.unshift(ticket);
      },
      editTicket(id) {
        console.log('Edit ticket ' + id);
      },
      changePage() {
        this.fetchData(this.tickets.current_page);
      },
    }
  }
</script>

<style lang="scss">
  .table.b-table .status-badge {
    min-width: 100px;
    font-size: 16px;
  }
</style>


